.tur-card-saldo {
  background-color:#0869ff;
  padding: 15px;
  border-radius: 4px;
  color: #e5ebfb;
  margin-bottom: 25px;
}

.tur-card-saldo h3 {
  margin-bottom: 0;
  font-size: 24px;
  color: #ffffff;
}

.tur-balance__item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  align-items: center;
  padding: 15px;
  background-color: #fae8e7;
  color: #ef5c45;
  margin-bottom: 10px;
  border-radius: 4px;
}

.tur-balance__item--in {
  background-color: #e6f5ee;
  color: #53c986;
}

.tur-balance__icon {
  width: 15%;
}

.tur-balance__content {
  width: 85%;
}

.tur-balance__content > span {
  display: block;
}

.tur-balance__price {
  font-size: 18px;
  font-weight: 500;
  padding-bottom: 5px;
}

.tur-balance__desc {
  font-size: 12px;
  position: relative;
  color: #797979;
}

.tur-balance__desc span:last-child {
  float: right;
}
